@mixin publishers__mockups {
  display: flex;
  flex-direction: column;
  position: relative;

  .mockup {
    @include mockup($button-colors: true);

    // mobile
    &:first-of-type {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 9.1em;

      // mobile iframe
      .mockup--android {
        perspective: 2000px;

        .mockup__iframe {
          transform: rotateY(40deg);
        }
      }
    }

    // browser
    &:last-of-type {
      @include mockup--placeholder($blue);

      perspective: 1000px;

      .mockup--browser {
        overflow: hidden;
        transform: rotateY(-10deg);
      }
    }
  }


  @include publishers__mockups--landscape;
  @include publishers__mockups--desktop;
  @include publishers__mockups--fullhd;
  @include publishers__mockups--tablet-landscape;
  @include publishers__mockups--tablet-portrait;
  @include publishers__mockups--ipad-pro;
  @include publishers__mockups--mobile;
  @include publishers__mockups--mobile-large;
  @include publishers__mockups--mobile-xl;
}
