@mixin mockup--android($width: 9.3em, $top: 12.2%, $left: 1%, $padding: 142%) {
  .figure {
    @include figure($stretch: stretched);
  }

  .mockup__iframe {
    @include mockup__iframe($width, 2, 3);

    width: auto;
    top: $top;
    left: $left;
    position: absolute;

    .intrinsic-ratio-container {
      padding-bottom: $padding;
    }
  }
}
