@mixin footer-top {
  @include articles--grid(2, $lem * 4);

  @include ie {
    @include articles--grid(2, $lem * 4, $iepx: 0.01em);
  }

  @supports (-ms-ime-align:auto) {
    @include articles--grid(2, $lem * 4, $iepx: 0.01em);
  }
  
  @include border(normal, bottom);
  border-color: $gray-format;
  padding-bottom: $lem * 2.541;

  &__links {
    @include footer-top__links;
  }

  &__contact {
    @include footer-top__contact;
  }

  @include footer-top--mobile;
}
