@mixin publishers__mockups--tablet-portrait {
  @include tablet-portrait {
    height: calc(56.24vw + #{$lem * 3});
    margin: $lem 0;

    align-items: center;
    justify-content: center;

    .mockup {
      &:first-of-type {
        transform: scale(.95) translateX(1.26em) translateY(10em);

        .mockup--android {
          @include mockup--android(9.3em, 12%, 1%, 143%);
        }
      }

      &:last-of-type {
        transform: translateX($lem * 2);

        .mockup__iframe {
          @include mockup__iframe(85vw, 16, 9);
        }
      }
    }
  }
}
