@mixin rich-media-hero__image {
  .figure {
    @include figure;

    .img {
      @include rich-media-hero--resize($lem * 3);
    }
  }

  @include rich-media-hero__image--mobile;
  @include rich-media-hero__image--mobile-large;
  @include rich-media-hero__image--mobile-xl;
  @include rich-media-hero__image--tablet;
  @include rich-media-hero__image--landscape;
  @include rich-media-hero__image--tablet-landscape;
  @include rich-media-hero__image--ipad-pro;
  @include rich-media-hero__image--desktop;
  @include rich-media-hero__image--fullhd;
}
