@mixin publishers__mockups--fullhd {
  @include fullhd {
    .mockup {
      &:first-of-type {
        transform: scale(1.12) translateY(14.5em);

        .mockup__iframe {
          min-width: 9.4em;
        }
      }

      &:last-of-type {
        transform: translateX($lem * 3) translateY(2.2em);
      }
    }
  }
}
