@mixin publishers__mockups--mobile {
  @include mobile {
    height: calc(56.24vw + #{$lem});
    margin: $lem 0;

    .mockup {
      &:first-of-type {
        transform: scale(.8) translateX(0em) translateY(4em);

        .mockup--android {
          @include mockup--android(4.7em, 11%, -10%, 125%);

          .figure {
            .img {
              max-width: 80px; // iOS retina bugfix
            }
          }

          .mockup__iframe {
            transform: rotateY(50deg);
          }
        }
      }

      &:last-of-type {
        transform: translateX($lem);

        .mockup--browser {
          .mockup__buttons {
            padding: $lem / 6;

            .circle {
              @include circle($lem / 8);
            }
          }

          .mockup__iframe {
            @include mockup__iframe(85vw, 16, 9);
          }
        }
      }
    }
  }
}
